import React from 'react';
import ReactDOM from 'react-dom';
import { List, SegmentedControl } from 'jw-components-mobile';

const SegmentedControlExample = React.createClass({
  onChange(e) {
    console.log(`selectedIndex:${e.nativeEvent.selectedSegmentIndex}`);
  },
  onValueChange(value) {
    console.log(value);
  },
  render() {
    return (
      <div className="sc-example example">
        <SegmentedControl className="jw-segment" values={['1', '2','3', '4']}  
         tintColor={'#157efb'} />
        <SegmentedControl className="jw-segment" values={['1', '2','3','4']} 
         disabled tintColor={'#4174d9'} />
        <SegmentedControl className="jw-segment" values={['1', '2', '3','4']} 
          selectedIndex={1} 
          tintColor={'#0ab771'}/>
        <SegmentedControl className="jw-segment" values={['1', '2', '3','4']} 
         tintColor={'#f2bb16'}/>
        <SegmentedControl className="jw-segment"  values={['1', '2', '3','4']}
          onChange={this.onChange}
          onValueChange={this.onValueChange}
          tintColor={'#777777'}/>
      </div>
    );
  },
});
ReactDOM.render(<SegmentedControlExample  />, document.getElementById('root'));



